import React, {Component} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css'
import OpportunityCart from './OpportunityCart'
import Opportunity from './Opportunity';
import {Card, CardBody} from 'reactstrap';
import {EventConsumer} from '../context';


export default class Cart extends Component {
    render() {
        return (
            <div className="row">
                <div className="col">
                    <div className="mx-auto">
                        <Card style={cardStyle}>
                            <CardBody>
                                <p style={eventNameStyle}>Level 2 Sponsorship</p>
                                <p style={companyStyle}>This package includes your name listed as a sponsor on our website and a few posters throughout the venue to get participants interested in your company!"</p>
                                <div className="row">
                                    <div className="col">
                                        <p style={dateStyle}>$100</p>
                                    </div>
                                </div>
                            </CardBody>
                        </Card>
                    </div>
                    <div className="mx-auto">
                        <Card style={cardStyle}>
                            <CardBody>
                                <p style={eventNameStyle}>Level 1 Sponsorship</p>
                                <p style={companyStyle}>This package includes your name listed as a sponsor on our website and a few posters throughout the venue to get participants interested in your company!"</p>
                                <div className="row">
                                    <div className="col">
                                        <p style={dateStyle}>$100</p>
                                    </div>
                                </div>
                            </CardBody>
                        </Card>
                    </div>
                </div>
                <div className="col">
                    <h1>Total</h1>
                    <h2>$150</h2>
                </div>
            </div>
        );
    }
}

const dateStyle = {
    color: "#063FA3",
};
const companyStyle = {
    color: '#6B6B6B'
};
const eventNameStyle = {
    color: '#000000'
};
const cardStyle = {
    width: 1000,
    height: 200
};

const cartBtn = {
    background: "transparent",
    borderRadius: "0.3em",
    color: "blue",
    bottom: "20",
};

const col = {
    marginLeft: 350
};